import React from "react";

const List = () => {
  return (
    <div class="d-flex justify-content-between mb-4">
      <p class="sk_line sk_line_height_10 sk_line_30 m-0"></p>
      <p class="sk_line sk_line_height_10 sk_line_30 m-0"></p>
    </div>  
  );
};

const PopupListSkeleton = () => {
  return [1, 2, 3, 4].map(() => {
    return (
      <div class="card mb-3 p-3">
        <div class="row ">
          <div class="col-lg-2">
            <div class="sk_line sk_img_thumb rounded-0 w-100 m-0 me-5"></div>
          </div>
          <div class="col-lg-10">
            <div className="d-flex flex-grow-1 w-100 mb-8 justify-content-between align-items-center">
              <div class="d-flex align-items-center w-100">
                <div className="d-flex align-items-center w-50 m_grid_author">
                  <div class="sk_line sk_img m-0 me-3"></div>
                  <div class="w-50">
                    <h3 class="sk_line sk_line_height_16 sk_line_90 m-0 mb-3"></h3>
                    <p class="sk_line sk_line_height_10 sk_line_60 m-0"></p>
                  </div>
                </div>
                <div class="sk_line sk_line_10 sk_line_height_16 m-0"></div>
              </div>
              <div class="sk_line sk_line_10 sk_line_height_16 m-0"></div>
            </div>

            <h6 class="sk_line sk_line_height_10 sk_line_100 m-0 mb-3"></h6>
            <p class="sk_line sk_line_height_10 sk_line_20 m-0 mb-5"></p>
            <div class="row mt-lg-0 mt-2">
              <div class="col-sm-6">
                <List/>
                <List/>
                <List/>
                <List/>
              </div>
              <div class="col-sm-6">
                <List/>
                <List/>
                <List/>
                <List/>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  });
};

export default React.memo(PopupListSkeleton);
